import LoginIn from './components/LoginIn.vue'
import Logined from './components/Logined.vue'
import Register from './components/Register.vue'

export default[
    {   path: "/", 
        name:'LoginIn',
        component:LoginIn
    },
    {   path:"/login/",
        name:'Logined',
        component:Logined
    },
    {   path:"/register",
        name:"Register",
        component:Register
    }
]


