<template>
  <div class="login-register">
    <div class="contain">
      <div class="big-box">
        <div class="big-contain">
          <h2 class="scontent">欢迎来到登录系统</h2>
          <form class="bform">
            <label class="btitle">账号密码登录</label><br>
            <label class="btitle">账号</label>
            <input type = "text" v-model = "inputaccount" required placeholder="输入手机号/邮箱"/>
            <span class="errTips" v-if="emailError">* 账号不存在 *</span><br>
            <label class="btitle">密码</label>
            <input type = "password" v-model="inputpassword" required placeholder="输入密码" />
            <span class="errTips" v-if="passwordError">* 密码填写错误 *</span><br>
            <button class="bbutton" v-on:click.prevent="check">登录</button>
            <router-link to="/register" class="sbutton">没有账号？立即注册</router-link>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login-in',
  data () {
    return {
      account:{
        name:"",
        email:"",
        phone:"",
        password:""
      },
      emailError: false,
			passwordError: false,
      inputaccount:"",
      inputpassword:"",
    }
  },
  methods:{
    check:function(){
        const self = this;
        if(self.inputaccount != "" && self.inputpassword != "") {
          self.$axios({
            method:'post',
            url: 'http://127.0.0.1:10520/api/user/login',
						data: {
							account: self.inputaccount,
							password: self.inputpassword
						}
          })
          .then( res => {
              switch(res.data){
						  	case -1:
							  	this.emailError = true;
							  	this.passwordError = false;
							  	break;
						  	case 1:
							  	this.passwordError = true;
							  	this.emailError = false;
							  	break;
                default:
                  this.emailError = false;
							  	this.passwordError = false;
                  this.account.name = res.data.name;
                  this.$router.push({
                    name:'Logined',
                    query:{
                      id : this.account.name
                    }
                  })
							  	break;
              }
          })
          .catch( err => {
						console.log(err);
          })
        } else{
					alert("填写不能为空！");
        }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="scoped">
	.login-register{
		width: 100vw;
		height: 100vh;
		box-sizing: border-box;
	}
  .contain{
		width: 60%;
		height: 60%;
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		background-color: rgb(196, 243, 237);
		border-radius: 20px;
		box-shadow: 0 0 3px #f0f0f0,
					0 0 6px #f0f0f0;
	}
  .big-box{
		width: 70%;
		height: 80%;
		position: absolute;
		top: 0;
		left: 15%;
	}
  .big-contain{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
  .scontent{
		font-size: 2em;
		color: rgb(104, 193, 235);
		text-align: center;
		line-height: 1.7em;
	}
  .bform{
    margin-top:0;
		width: 100%;
		height: 40%;
		padding: 2em 0;
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		align-items: center;
	}
  .bform .errTips{
		display: block;
		color: red;
		font-size: 0.7em;
		margin-left: 1em;
    text-align: center;
	}
  .bform input{
		width: 50%;
		height: 30px;
		border:aquamarine;
		outline: none;
		border-radius: 20px;
		padding-left: 2em;
		background-color: #f0f0f0;
	}
  .btitle{
		font-size: 0.8em;
		font-weight: bold;
		color: rgb(57,167,176);
	}
  .bbutton{
		width: 20%;
		height: 40px;
		border-radius: 24px;
		border: none;
		outline: none;
		background-color: rgb(57,167,176);
		color: #fff;
		font-size: 0.9em;
		cursor: pointer;
	}
  .sbutton{
    margin-top: 20px;
		width: 40%;
		height: 40px;
		border-radius: 24px;
		border: 1px solid #fff;
		outline: none;
		background-color: rgb(207, 236, 184);
		color: rgb(187, 216, 21);
		font-size: 0.9em;
		cursor: pointer;
    text-align: center;
	}
</style>



