<template>
    <div class="login-register">
    <div class="contain">
    <div class="big-box">
    <div class="big-contain">
        <h2 class="scontent">登录成功，欢迎, {{username}}</h2>
        <button v-on:click.prevent="quit" class="bbutton">点击退出</button>
    </div>
    </div>
    </div>
    </div>
</template>
<script>
export default {
    name:'logined',
    data () {
        return {
            username:""
        }
    },
    methods: {
        getParams(){
			this.username = this.$route.query.id;
            //console.log(this.username);
		},
        quit:function(){
            this.logined = false;
            this.$router.replace({
            path:'/'
        })
    }
    },
    created(){
	    this.getParams();
    }
}
</script>

<style scoped="scoped">
	.login-register{
		width: 100vw;
		height: 100vh;
		box-sizing: border-box;
	}
  .contain{
		width: 60%;
		height: 60%;
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		background-color: rgb(196, 243, 237);
		border-radius: 20px;
		box-shadow: 0 0 3px #f0f0f0,
					0 0 6px #f0f0f0;
	}
  .big-box{
		width: 70%;
		height: 50%;
		position: absolute;
		top: 0;
		left: 15%;
	}
  .big-contain{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
    .scontent{
		font-size: 2em;
		color: rgb(104, 193, 235);
		text-align: center;
		line-height: 1.7em;
	}
    .bbutton{
		width: 20%;
		height: 40px;
		border-radius: 24px;
		border: none;
		outline: none;
		background-color: rgb(57,167,176);
		color: #fff;
		font-size: 0.9em;
		cursor: pointer;
	}
</style>



