<template>
    <div class="login-register">
    <div class="contain">
    <div class="big-box">
    <div class="big-contain">
        <h2 class="scontent">注册，开始我们的旅行</h2>
        <form v-if="!registed" class="bform">
        <label class="btitle">用户名</label>
        <input type = "text" v-model = "account.name" required placeholder="输入用户名"/>
        <span class="errTips" v-if="nameError">* 用户名已存在 *</span><br>

        <label class="btitle">手机号</label>
        <input type = "text" v-model = "account.phone" required placeholder="输入手机号"/>
        <span class="errTips" v-if="phoneError">* 手机号已存在 *</span><br>

        <label class="btitle">邮箱</label>
        <input type = "text" v-model = "account.email" required placeholder="输入邮箱"/>
        <span class="errTips" v-if="emailError">* 邮箱已存在 *</span><br>

        <label class="btitle">密码</label>
        <input type = "password" v-model = "password1" required placeholder="输入密码"/><br>
        <label class="btitle">确认密码</label>
        <input type = "password" v-model= "password2" required placeholder="确认密码" />
        <span class="errTips" v-if="passwordError">* 两次密码不一致 *</span><br>

        <button v-on:click.prevent="registe" class="bbutton">注册</button>
        <router-link to="/" class="sbutton">已有账号？立即登录</router-link>
        </form>

        <div v-if="registed">
            <h3>注册成功，立即登录吧~</h3>
            <router-link to="/"><button class="bbutton">登录</button></router-link>
        </div>
    </div>
    </div>
    </div>
    </div>
</template>
<script>
export default {
    name:'register',
    data () {
        return {
            account:{
                name:"",
                email:"",
                phone:"",
                password:""
            },
            nameError:false,
            phoneError:false,
            emailError:false,
            passwordError:false,
            password1:"",
            password2:"",
            registed:false

        }
    },
    methods: {
       registe:function(){
           const self = this;
           if(this.password1 === this.password2){
               this.account.password = this.password2;
               this.passwordError = false;
           }else{
               this.passwordError = true;
               return;
           }

           if(self.account.name != "" && self.account.email != "" && self.account.phone != "" && self.account.password != ""){
					self.$axios({
						method:'post',
						url: 'http://127.0.0.1:10520/api/user/add',
						data: {
							name: self.account.name,
                            phone: self.account.phone,
							email: self.account.email,
							password: self.account.password
						}
					})
					.then( res => {
						switch(res.data){
							case 0:
                                this.nameError = false;
                                this.emailError = false;
                                this.phoneError = false;
								alert("注册成功！");
                                this.registed = true;
								break;
							case 1:
								this.nameError = true;
								break;
                            case 2:
                                this.nameError = false;
								this.phoneError = true;
								break;
                            case 3:
                                this.nameError = false;
								this.phoneError = false;
								this.emailError = true;
								break;
						}
					})
					.catch( err => {
						console.log(err);
					})
				} else {
					alert("填写不能为空！");
				}
       }
    }
   
}
</script>

<style scoped="scoped">
	.login-register{
		width: 100vw;
		height: 100vh;
		box-sizing: border-box;
	}
  .contain{
		width: 60%;
		height: 80%;
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		background-color: rgb(196, 243, 237);
		border-radius: 20px;
		box-shadow: 0 0 3px #f0f0f0,
					0 0 6px #f0f0f0;
	}
  .big-box{
		width: 70%;
		height: 50%;
		position: absolute;
		top: 0;
		left: 15%;
	}
  .big-contain{
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
  .scontent{
		font-size: 2em;
		color: rgb(104, 193, 235);
		text-align: center;
		line-height: 1.7em;
	}
  .bform{
        margin-top: 0;
		width: 100%;
		height: 40%;
		padding: 0 0;
		display: flex;
		flex-direction: column;
		align-items: center;
	}
  .bform .errTips{
		display: block;
		color: red;
		font-size: 0.7em;
		margin-left: 1em;
    text-align: center;
	}
  .bform input{
		width: 50%;
		height: 30px;
		border:aquamarine;
		outline: none;
		border-radius: 20px;
		padding-left: 2em;
		background-color: #f0f0f0;
	}
  .btitle{
		font-size: 0.8em;
		font-weight: bold;
		color: rgb(57,167,176);
	}
  .bbutton{
		width: 20%;
		height: 40px;
		border-radius: 24px;
		border: none;
		outline: none;
		background-color: rgb(57,167,176);
		color: #fff;
		font-size: 0.9em;
		cursor: pointer;
	}
  .sbutton{
    margin-top: 20px;
		width: 40%;
		height: 40px;
		border-radius: 24px;
		border: 1px solid #fff;
		outline: none;
		background-color: rgb(207, 236, 184);
		color: rgb(187, 216, 21);
		font-size: 0.9em;
		cursor: pointer;
    text-align: center;
	}
</style>



